import styled, { css } from "styled-components";

export const Container = styled.a`
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  ${({ theme }) => css`
  
    background-color: ${theme.neutrals.pureWhite};
    outline-color: ${theme.neutrals.gray3};
    border-radius: ${theme.borderRadius.lg}px;
    box-shadow: ${theme.brand.boxShadow.level2};
    transition: all ease-in-out 300ms;
    header {
      color: ${theme.brand.color.primary.dark};
      padding: ${theme.spacing.xxs}px ${theme.spacing.xxxs}px ${theme.spacing.md}px;
    }
    footer {
      margin-top: 20px;
      .buttons {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 20px;
        display: flex;
        flex-direction: columm;
        padding: ${theme.spacing.xs}px;
      }
    }
    &:hover {
      box-shadow: ${theme.brand.boxShadow.level4};
    }
    @media screen and (max-width: ${theme.brand.media_query.tablet}) {
      width: 178px;
    }
    @media screen and (max-width: ${theme.brand.media_query.mobile}) {
      width: 100%;
      header p {
        font-size: ${theme.fontSize.xs}px !important;
      }
    }
  `}
`;
