import React, { useState } from 'react';
import { Typography, useTheme } from '@stardust-ds/react';
import { TContract } from 'types/contract';
import { getPath } from 'utils/pathHandler';
import { Container, SeeMore } from './styles';
import ContractCard from 'components/ContractCard';

interface IProps {
  contract: TContract;
}

const Item: React.FC<IProps> = ({contract}) => {
  const theme = useTheme();
  const [active, setActive] = useState(false);

  return (
    <Container>
      <ContractCard
          href={getPath('contract').replace(':id', contract.id)}
          description={contract.description}
          description2={contract.description2}
        />
      <SeeMore isActive={active}>
        <Typography color={theme.brand.color.primary.pure} textAlign='center' fontSize='md'>
          {contract.items[0].description}
        </Typography>
        <Typography color={theme.brand.color.primary.pure} textAlign='center' fontWeight="regular" fontSize='sm'>
        {contract.items[1].description}
        </Typography>
        <Typography color={theme.brand.color.primary.pure} textAlign='center' fontWeight="regular" fontSize='sm'>
        {contract.items[2].description}
        </Typography>
      </SeeMore>
      <button className="see-more" onClick={() => setActive(!active)}>
        <Typography color={theme.brand.color.primary.medium} fontWeight='medium' textAlign='center' fontSize='sm'>
          {active? 'Ver menos' : 'Ver mais'}
        </Typography>
      </button>
    </Container>
  );
}

export default Item;