import styled, { css } from "styled-components";
import { PrivateContainer } from "styles/global";

export const Container = styled(PrivateContainer)`
${({ theme }) => css`
background-color: ${theme.neutrals.gray1};
display: flex;
flex-direction: column;
.content{
  padding: 50px 100px 75px;
  @media screen and (max-width: ${theme.brand.media_query.tablet}) {
    padding: 50px 30px 75px;
  }
  @media screen and (max-width: ${theme.brand.media_query.mobile}) {
    padding: 50px 30px 75px;
  }
}
.itens {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
}
`}
`;

