import { Typography, useTheme } from '@stardust-ds/react';
import Lottie from 'lottie-react';
import React from 'react';

import { Container } from './styles';

const LoadAnimation: React.FC = () => {
  const theme = useTheme();

  return (
    <Container>
        <Typography color={theme.brand.color.primary.medium} fontSize='lg'>
          Só um pouquinho!
        </Typography>
        <Typography color={theme.brand.color.primary.medium} fontSize='xs' fontWeight="regular">
          Estamos carregando suas informações
        </Typography>
      <Lottie
        animationData={require('assets/load_Animation.json')}
        className='animation-content'
      />
    </Container>
  );
}

export default LoadAnimation;