import { useState } from "react";
import { Typography, useTheme } from "@stardust-ds/react";
import { useDebouncedEffect } from "@react-hookz/web";
import { Container } from "./styles"
import { shallow } from "zustand/shallow";
import { IContractsResponse } from "interfaces/contract/IContractsRespopnse";
import { TContract } from "types/contract";
import useRouteHandler from "hooks/useRouteHandler";

import useStore from "hooks/useStore";
import Item from "./Item";
import LoadAnimation from "components/LoadAnimation";

const Contracts: React.FC = () => {
  const theme = useTheme();
  const { to } = useRouteHandler();
  const { api } = useStore(state => 
    ({
      api: state.getApi(),
    }), shallow);
  
  const [isLoading, setIsLoading] = useState(false);
  const [contrats, setContracts] = useState<TContract[]>([]);
    
  useDebouncedEffect(async () => {
    setIsLoading(true)
    try {
      const { data } = await api.get<IContractsResponse>('contracts');
      
      if(data.items.length === 1 ){
        const id = data.items[0].id;
        to('contract', {
          id: id,
          replace: true
        })
      }
      setContracts(data.items);
    } finally {
      setIsLoading(false);
    }
  }, [api], 300);

    return (
      <Container>
      {
        isLoading? <LoadAnimation /> :
        <>
        <div className="content">
          <div>
            <Typography color={theme.brand.color.primary.medium} fontSize='lg'>
              Selecionar contrato
            </Typography>
            <Typography color={theme.brand.color.primary.medium} fontSize='xs' fontWeight="regular">
              Clique no contrato desejado para visualiza-lo
            </Typography>
          </div>
          <div className="itens">
            {
              contrats.map(c => (
                <Item contract={c}/>
              ))
            }
          </div>
        </div>
        </>
        }
      </Container>
    );
  }
  
  export default Contracts;