import { Button, Typography, useTheme } from '@stardust-ds/react';
import { Container } from './styles';
import Icon from 'assets/icons/Icon';
import useRouteHandler from 'hooks/useRouteHandler';
import React from 'react';

interface IProps {
  href?: React.HTMLProps<HTMLAnchorElement>['href'];
  onClick?: React.HTMLProps<HTMLAnchorElement>['onClick'];
  className?: React.HTMLProps<HTMLAnchorElement>['className'];
  description?: string;
  description2?: string;
  state?: any;
  footer?: boolean;
}

const ContractCard: React.FC<IProps> = ({ description, description2, state, footer, ...rest }) => {
  const theme = useTheme();
  const { handleClickHref, to } = useRouteHandler();
  return (
    <Container onClick={(e) => handleClickHref(e, false, state)} {...rest} >
      <header>
        <Typography fontWeight='extraBold' fontSize='sm' type='p1' textAlign='center' mBottom='sm'>
          {description}
        </Typography>
        <Typography fontWeight='bold' fontSize='xxl' type='p1' textAlign='center' color={theme.brand.color.primary.medium}>
          {description2}
        </Typography>
      </header>
      {
        footer?
        <footer>
        <hr/>
        <div className='buttons'>
          <Button 
            onClick={() => to('contracts')}
            iconLeft={<Icon
              icon={'arrows_rotate-light'}
              size={theme.fontSize.xs}
            />} 
            bgColor={theme.brand.color.secondary.pure}>
            SELECIONAR OUTRO CONTRATO
          </Button>
          <Button 
            iconLeft={<Icon
              icon={'clock_rotate_left-light'}
              size={theme.fontSize.xs}
          />} 
            bgColor={theme.brand.color.secondary.pure}>
            VER HISTÓRICO DE PAGAMENTOS
          </Button>
        </div>
      </footer> : 
      <></>
      }
    </Container>
  );
}

export default ContractCard;